#landing {
  height: 100vh;
  background-repeat: no-repeat;
  background-position: center;
  animation: landBgChanger 100s infinite;
  background-image: url("../../../img/landing-img/nature-bg1.jpg");
  filter: grayscale(60%);
  opacity: 0.7;
}

.landingContainer {
  position: absolute;
  top: 28%;
  left: 50%;
  translate: -50% 0;
  text-align: center;
}
.landingContainer h1 {
  line-height: 70px !important;
  white-space: nowrap;
}

@media screen and (max-width: 480px) {
  .landingContainer {
    top: 15%;
    left: 50%;
  }
  .landingContainer div {
    text-align: start !important;
  }
  .landingContainer h1 {
    white-space: unset;
    font-size: 80px;
  }
  .landingContainer h1:last-child {
    font-size: 60px;
  }
}
@keyframes landBgChanger {
  0% {
    background-image: url("../../../img/landing-img/nature-bg1.jpg");
  }
  5% {
    background-image: url("../../../img/landing-img/nature-bg2.jpg");
